body {
  font-family: var(--main-font);
  font-size: 16px;
  letter-spacing: 0.04em;

  color: var(--main-light);
  background-color: var(--main-background);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);

  transition: opacity var(--main-anim), visibility var(--main-anim);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}
